




















































































import { Component, Vue, Ref } from "vue-property-decorator";
import CallToAction from "../CallToAction.vue";
import FixedHeader from "../../components/header/FixedHeader.vue";
import Api from "../../Api";
@Component({
  components: {
    CallToAction,
    FixedHeader,
  },
})
export default class OurTeam extends Vue {
  ourTeam = [
    {
      id: 0,
      teamMemberImage: "",
      teamMemberName: "",
      teamMemberProfition: "",
      teamMemberLinkedIn: "",
      teamMemberBrief: "",
    },
  ];
  created() {
    this.fetchTeam();
  }
  fetchTeam() {
    fetch(Api.OurTeam.GetAll)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          console.log("response is :" + response);
        }
      })
      .then((data) => {
        this.ourTeam = [];
        let result = data.data;
        for (const id in result) {
          this.ourTeam.push({
            id: parseInt(id),
            teamMemberImage: Api.LocalhostImages + result[id].OT_Image,
            teamMemberName: result[id].OT_Name,
            teamMemberProfition: result[id].OT_Profition,
            teamMemberLinkedIn: result[id].OT_LinkedIn_Url,
            teamMemberBrief: result[id].OT_Brief,
          });
        }
        console.log(data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }
}
